.search-form {
  height: 60px;
  text-align: center;
  z-index: 99;
  width: 100%;
  background-image: linear-gradient(90deg, #788ba1 15%, rgba(161, 120, 132, .7) 40%, rgba(123, 120, 161, .6) 75%, rgba(213, 117, 63, .7));
  padding: 5px;
  position: fixed;
}

body {
  width: 100%;
  position: absolute;
}

input, .search-btn {
  letter-spacing: .06em;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(211, 125, 75, .79);
  border-radius: 7px;
  padding: 10px 32px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.88;
  transition: background-color .75s cubic-bezier(.4, 0, .8, 1);
}

.search-btn:hover, .search-btn:focus, .load-more:focus, .load-more:hover {
  background-color: rgba(211, 125, 75, .79);
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  margin: 100px 20px 20px;
  display: flex;
  position: relative;
}

.photo-card img {
  width: 300px;
  height: 200px;
}

.info {
  margin-left: 10px;
}

.info-item {
  height: 20px;
  letter-spacing: .06em;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 300;
  line-height: 1.88;
}

.load-more {
  letter-spacing: .06em;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(211, 125, 75, .79);
  border-radius: 7px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 32px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.88;
  transition: background-color .75s cubic-bezier(.4, 0, .8, 1);
  display: flex;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.big-photo-ref {
  color: inherit;
  transition: box-shadow 250ms var(--time-cube);
  margin-bottom: 30px;
  padding-bottom: 7px;
  text-decoration: none;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 1px -1px rgba(0, 0, 0, .12);
}

.big-photo-ref:hover, .big-photo-ref:focus {
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

